
.news {
  display: block;
  margin-bottom: 50px;
}
.news h3 {
  text-indent: 1rem;
  margin-bottom: 0;
}
.news .news-item {
  margin: 10px 0px;
  padding: 10px;
  background: #f6f8fa;
  border-radius: 15px;
}
.news .news-item .news-title {
  font-size: 16px;
  display: inline-block;
  color: #656b79;
}
.news .news-item .news-img {
  width: 100%;
  margin: 5px 0;
  border-radius: 10px;
}
.news .news-item .news-time {
  font-size: 14px;
  line-height: 20px;
  color: #999;
  margin-top: 7px;
  text-indent: 3px;
}



