
.clearfix:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
/* Hides from IE-mac \*/
* html .clearfix {
  height: 1%;
}
/* End hide from IE-mac */
.box {
  width: 100%;
  height: 100%;
  padding: 20px 0;
  margin: 10px 0;
  background: #f6f8fa;
  border-radius: 15px;
}
.box .item {
  width: 25%;
  float: left;
  height: 60px;
  text-align: center;
  color: #656b79;
  text-decoration: none;
  font-size: 12px;
}
.box .item i {
  width: 35px;
  height: 35px;
  border-radius: 50px;
  display: block;
  margin: 5px auto;
  background-size: 100%;
}



