
.content {
  width: 100%;
  -webkit-transform: scale3d(1, 1, 1);
          transform: scale3d(1, 1, 1);
}
.content .title {
  width: 100%;
  background: #3e4243;
  height: 40px;
  line-height: 40px;
  font-size: 16px;
  text-align: center;
  color: #fff;
  border-radius: 5px;
  position: fixed;
  top: 0px;
}
.content .contentB {
  text-align: center;
}
.content .contentB .input-text {
  border: 1px solid #bfcbd9;
  border-radius: 4px;
  color: #1f2d3d;
  font-size: 12px;
  height: 26px;
  outline: 0;
  padding: 3px 10px;
  width: 80%;
  margin: 20px auto;
}
.content .contentB .button {
  display: inline-block;
  background: #20a0ff;
  color: #fff;
  height: 20px;
  font-size: 12px;
  border-radius: 4px;
  text-align: center;
  line-height: 20px;
  padding: 5px 20px;
  cursor: pointer;
}
.content .contentB .show-content {
  margin: 20px;
  font-size: 12px;
  color: #3e4243;
}



