
.nav-box {
  position: fixed;
  bottom: 0;
  width: 100%;
  background: #fff;
  border-top: 1px solid #f0f0f0;
  height: 40px;
}
.nav-box .nav-list {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  z-index: 10;
  height: 40px;
  margin: 0px;
}
.nav-box .nav-list .nav-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 40px;
  text-align: center;
  display: block;
  text-decoration: none;
}
.nav-box .nav-list .nav-item i {
  height: 100%;
  width: 90px;
  display: block;
  margin: 0 auto;
  background-repeat: no-repeat;
  background-position-x: center;
  background-position-y: center;
  background-size: 120%;
  position: relative;
  top: 0px;
}
.nav-box .nav-list .nav-item .index1 {
  background-image: url("../assets/img/shop-1-b.png");
}
.nav-box .nav-list .nav-item .index {
  background-image: url("../assets/img/shop.png");
}
.nav-box .nav-list .nav-item .my1 {
  background-image: url("../assets/img/center-1-b.png");
}
.nav-box .nav-list .nav-item .my {
  background-image: url("../assets/img/center.png");
}



