
.my-head {
  width: 100%;
  height: 150px;
  background: #3e4243;
  position: relative;
  border-radius: 10px;
}
.my-head .my-logo {
  display: block;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: url("../assets/img/logo.png") no-repeat;
  position: absolute;
  top: 30px;
  left: 0;
  right: 0;
  margin: 0px auto;
}
.my-head .my-name {
  display: block;
  position: absolute;
  bottom: 15px;
  right: 0;
  left: 0;
  margin: 0px auto;
  text-align: center;
  color: #fff;
}
.my-detail {
  margin: 10px 0;
  background: #f6f8fa;
  padding: 15px;
  border-radius: 10px;
}
.my-detail a {
  text-decoration: none;
  color: #a5a5a5;
}



